import React, { Component } from 'react';
import Layout from '../components/Layout';

class _404 extends Component {
    render() {
        return (
            <Layout {...this.props}>
                <h1>
                    404 Not Found!
                    </h1>
            </Layout>
        );
    }
}

export default _404;



